.products {
  margin-top: 50px;
  margin-bottom: 50px;

  @media (min-width: $md) {
    margin-top: 90px;
    margin-bottom: 90px;
  }

  &__header {
    position: relative;
    margin-bottom: 20px;

    @media (min-width: $xl) {
      margin-bottom: 30px;
    }
  }

  &__title {
    padding-right: 115px;

    @media (min-width: $md) {
      padding-right: 135px;
    }

    @media (min-width: $xl) {
      padding-right: 175px;
    }
  }

  &__show-more {
    transform: translateY(-50%);
    position: absolute;
    top: 50%;
    right: 0;
    display: flex;
    align-items: center;
    gap: 5px;

    @media (any-hover: hover) {
      &:hover .products__show-more-icon {
        transform: rotate(180deg);
      }
    }

    &-icon {
      width: 12px;
      height: 12px;
      transform: rotate(135deg);
      transition: transform $transition;

      @media (min-width: $md) {
        width: 17px;
        height: 17px;
      }

      @media (min-width: $xl) {
        width: 24px;
        height: 24px;
      }
    }
  }

  &-main-slider {
    @media (max-width: $max-lg) {
      padding-bottom: 50px;
    }
  }
}

// Scrollbar
.swiper-scrollbar.swiper-scrollbar-horizontal {
  height: 10px;
  background-color: $white;
  border-radius: 0;

  @media (min-width: $lg) {
    display: none;
  }

  & .swiper-scrollbar-drag {
    top: -3px;
    height: 16px;
    background-image: url("../images/scrollbar-thumb.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    background-color: $txt-clr;
  }
}

// Product preview
.product-preview {
  position: relative;

  &__image {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 5px;
    padding: 15px;
    height: 175px;
    background-color: $white;

    @media (min-width: $md) {
      margin-bottom: 10px;
      height: 230px;
    }

    @media (min-width: $xl) {
      padding: 25px;
      height: 320px;
    }

    img {
      width: 90px;
      height: 90px;

      @media (min-width: $md) {
        width: 120px;
        height: 120px;
      }

      @media (min-width: $xl) {
        width: 145px;
        height: 145px;
      }
    }
  }

  &__to-cart {
    position: absolute;
    top: 15px;
    right: 15px;
    width: 18px;
    height: 18px;
    color: $secondary-clr;
    cursor: pointer;

    @media (min-width: $xl) {
      top: 25px;
      right: 25px;
    }
  }

  &__name {
    font-family: $font-title;
    font-size: 10px;

    @media (min-width: $md) {
      font-size: 12px;
    }

    @media (min-width: $xl) {
      font-size: 16px;
    }

    a {
      color: inherit;
    }
  }
}

.preview-image {
  &-src {
    @media (max-width: $max-xlg) {
      display: none;
    }

    @media (min-width: $xlg) {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      display: flex;
      font-size: 0;
    }

    &__link {
      @media (min-width: $xlg) {
        flex: 1 1 33.33%;
      }
    }
  }

  &-pagination {
    @media (max-width: $max-xlg) {
      display: none;
    }

    @media (min-width: $xlg) {
      position: absolute;
      bottom: 15px;
      gap: 7px;
      display: flex;
      color: $main-clr;
    }

    @media (min-width: $xl) {
      bottom: 25px;
    }

    &__item {
      svg {
        transition: $transition;
        fill: transparent;
      }

      &.active svg {
        fill: $main-clr;
      }
    }
  }
}
