// Mobile menu/Account
.mobile-menu {
  // Burger
  &__btn {
    &-burger {
      width: 20px;
      cursor: pointer;

      @media (min-width: $md) {
        width: 30px;
      }

      @media (min-width: $xl) {
        display: none;
      }

      rect {
        transition: $transition;
      }

      &:hover {
        rect{
          &:first-child {
            width: 30px;
          }

          &:nth-child(2) {
            width: 20px;
          }

          &:nth-child(3) {
            width: 10px;
          }

          &:nth-child(4) {
            width: 15px;
          }

          &:nth-child(5) {
            width: 25px;
          }
        }
      }
    }

    &-text {
      @media (max-width: $max-xl) {
        display: none;
      }

      @media (min-width: $xl) {
        display: flex;
        gap: 5px;
        font-size: 12px;
        line-height: normal;
        cursor: pointer;
        transition: $transition;
      }

      @media (min-width: $xxl) {
        font-size: 14px;
      }

      @media (any-hover: hover) {
        &:hover {
          color: $secondary-clr;
        }
      }

      &:hover {
        .mobile-menu__btn-text-icon {
          transform: rotate(45deg);
        }
      }

      &-icon {
        @media (min-width: $xl) {
          width: 10px;
          color: $secondary-clr;
          transition: $transition;
        }

        @media (min-width: $xxl) {
          width: 13px;
        }
      }
    }
  }

  // Menu
  &__menu {
    transform: translateX(-100%);
    position: absolute;
    top: 0;
    left: 0;
    width: 80vw;
    height: 100vh;
    background-color: $bg-clr;
    transition: $transition;

    @media (min-width: $md) {
      width: 50vw;
    }

    @media (min-width: $xl) {
      width: 25vw;
    }

    @media (min-width: $xxl) {
      width: 25vw;
    }
  }

  // List/Catalog
  &__list {
    &-item {
      margin-bottom: 15px;
      padding-bottom: 15px;
      border-bottom: 5px solid $white;

      @media (min-width: $md) {
        margin-bottom: 20px;
        padding-bottom: 20px;
      }

      @media (min-width: $xxl) {
        margin-bottom: 30px;
        padding-bottom: 30px;
      }
    }

    &-image {
      margin-bottom: 10px;
      height: 100px;
      overflow: hidden;

      @media (min-width: $md) {
        height: 120px;
      }

      @media (any-hover: hover) {
        &:hover img {
          transform: scale(1.1);
        }
      }

      img {
        transition: $transition;
      }
    }

    &-name {
      display: inline-flex;
      align-items: center;
      gap: 15px;
      color: $txt-clr;

      @media (any-hover: hover) {
        &:hover {
          .mobile-menu__list-icon {
            transform: rotate(45deg);
          }
        }
      }
    }

    &-icon {
      width: 13px;
      height: 13px;
      color: $secondary-clr;
      transition: $transition;
    }

    span {
      line-height: normal;
    }
  }

  // Nav
  &__nav {
    margin-top: 30px;

    &-item {
      margin-bottom: 20px;

      @media (min-width: $xxl) {
        margin-bottom: 30px;
      }
    }

    &-link {
      color: $txt-clr;
    }
  }
}

.is-open {
  .mobile-menu__menu {
    transform: translateX(0);
  }
}

// Mobile search
.search-mobile {
  @media (min-width: $xl) {
    display: none;
  }

  &__icon {
    @media (max-width: $max-xl) {
      position: absolute;
      top: 50%;
      left: 5px;
      transform: translateY(-50%);
      width: 20px;
      height: 20px;
      color: $gray;
      transition: $transition;
    }
  }

  .form__input {
    @media (max-width: $max-xl) {
      padding-left: 35px;
      height: 45px;

      &:focus + .search-mobile__icon {
        color: $secondary-clr;
      }
    }
  }
}
