.main-page-banner {
  height: calc(100vh - 59px);

  @media (min-width: $md) {
    height: 350px;
  }

  @media (min-width: $xl) {
    height: 600px;
  }

  @media (min-width: $xxl) {
    height: 780px;
  }

  .container {
    height: 100%;

    @media (max-width: $max-md) {
      padding-right: 0;
      padding-left: 0;
    }
  }
}

.main-page .main {
  @media (max-width: $max-md) {
    padding-top: 0;
  }
}
