// Fonts
$sans: 'Helvetica', 'Verdana', 'Arial', sans-serif;
$font-txt: 'Nunito-Extra-Light', $sans;
$font-title: 'Nunito', $sans;
$font-btn: 'Nunito-Semi-Bold', $sans;

// Breakpoints
$max-xs:  389px;
$xs:      390px;

$max-sm:  575px;
$sm:      576px;

$max-md:  767px;
$md:      768px;

$max-lg:  991px;
$lg:      992px;

$max-xlg:  1199px;
$xlg:      1200px;

$max-xl:  1439px;
$xl:      1440px;

$max-xxl: 1919px;
$xxl:     1920px;

// Colors
$white: #fff;
$black: #000;
$gray: #a4a4a4;

$bg-clr: #f9f5f1;
$header-bg-clr: #f6f1eb;
$footer-bg-clr: #2c2c2c;

$main-clr: #016275;
$secondary-clr: #967d66;

$txt-clr: #161616;
$txt-bg-clr: #f9f5f1;
$error-clr: #d91717;

// Other
$transition: .2s ease;
