.personal-message {
  margin-bottom: -50px;
  padding-top: 50px;
  padding-bottom: 50px;
  text-align: center;
  background-image: url("../images/personal-msg-bg.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  @media (min-width: $md) {
    margin-bottom: -75px;
    padding-top: 90px;
    padding-bottom: 90px;
  }

  @media (min-width: $xl) {
    margin-bottom: -110px;
    padding-top: 180px;
    padding-bottom: 180px;
  }

  @media (min-width: $xxl) {
    margin-bottom: -120px;
  }

  &__desc {
    margin-left: auto;
    margin-right: auto;
    max-width: 260px;

    @media (min-width: $md) {
      max-width: 360px;
    }

    @media (min-width: $lg) {
      max-width: none;
    }
  }

  &__content {
    display: flex;
    justify-content: center;
    gap: 5px;
    margin-top: 20px;

    @media (min-width: $md) {
      gap: 20px;
      margin-top: 30px;
    }

    @media (min-width: $xl) {
      gap: 60px;
      margin-top: 90px;
    }
  }
}

.personal-card {
  position: relative;
  width: 92px;
  height: 128px;
  cursor: pointer;

  @media (min-width: $xs) {
    width: 116px;
    height: 162px;
  }

  @media (min-width: $md) {
    width: 200px;
    height: 280px;
  }

  @media (min-width: $xl) {
    width: 340px;
    height: 475px;
  }

  img {
    object-fit: contain;
  }

  &__back,
  &__front {
    position: absolute;
    width: 100%;
    height: 100%;
    backface-visibility: hidden;
    transition: transform .4s linear;
  }

  &__back {
    transform: perspective(600px) rotateY(0deg);
  }

  &__front {
    transform: perspective(600px) rotateY(180deg);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    padding: 9px;
    background-color: $white;
    border-radius: 5px;
    border: 1px solid #bfd3ff;

    @media (min-width: $xs) {
      border-radius: 6px;
    }

    @media (min-width: $md) {
      padding: 15px;
      border-radius: 10px;
    }

    @media (min-width: $xl) {
      padding: 20px;
      border-width: 2px;
      border-radius: 16px;
    }

    &:before {
      content: '';
      transform: translate(-50%, -50%);
      position: absolute;
      left: 50%;
      top: 50%;
      width: calc(100% - 6px);
      height: calc(100% - 6px);
      border: 1px solid $secondary-clr;
      border-radius: 3px;

      @media (min-width: $xs) {
        border-radius: 4px;
      }

      @media (min-width: $md) {
        width: calc(100% - 12px);
        height: calc(100% - 12px);
        border-radius: 6px;
      }

      @media (min-width: $xl) {
        height: calc(100% - 16px);
        width: calc(100% - 16px);
        border-width: 2px;
        border-radius: 10px;
      }
    }

    &-decor {
      width: 20px;
      height: 20px;

      @media (min-width: $xs) {
        width: 30px;
        height: 30px;
      }

      @media (min-width: $md) {
        width: 50px;
        height: 50px;
      }

      @media (min-width: $xl) {
        width: 85px;
        height: 85px;
      }
    }

    &-msg {
      margin-bottom: 0;
      font-family: $font-title;
      font-size: 8px;

      @media (min-width: $md) {
        font-size: 12px;
      }

      @media (min-width: $xl) {
        font-size: 20px;
      }
    }
  }

  &.is-rotated .personal-card__back {
    transform: perspective(600px) rotateY(-180deg);
  }

  &.is-rotated .personal-card__front {
    transform: perspective(600px) rotateY(0deg);
  }
}
