.brand-section {
  margin-top: 50px;
  margin-bottom: 50px;

  @media (min-width: $md) {
    margin-top: 60px;
    margin-bottom: 60px;
  }

  @media (min-width: $xl) {
    display: flex;
    align-items: center;
    gap: 60px;
    margin-top: 90px;
    margin-bottom: 90px;
  }

  &--wide {
    @media (min-width: $xl) {
      flex-wrap: wrap;
    }

    .brand-section__image {
      @media (min-width: $xl) {
        width: 100%;
      }
    }

    .brand-section__desc {
      @media (min-width: $xl) {
        margin-right: auto;
        margin-left: auto;
        max-width: 960px;
      }
    }
  }

  &--half {
    .brand-section__image {
      @media (min-width: $xxl) {
        height: 700px;
      }
    }
  }

  &--no-img {
    margin-top: 0!important;
  }

  &--reverse {
    @media (min-width: $xl) {
      flex-direction: row-reverse;
    }
  }

  &__image {
    @media (max-width: $max-xl) {
      margin-bottom: 50px;
    }

    @media (min-width: $xl) {
      flex: 1 1 60%;
      margin-bottom: 0;
    }
  }

  &__desc {
    text-align: center;

    @media (min-width: $xl) {
      flex: 1 1 40%;
    }
  }
}
