.no-page {
  &-image {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 30px;
    font-size: 110px;
    font-family: $font-title;
    line-height: 1;

    @media (min-width: $md) {
      font-size: 180px;
    }

    @media (min-width: $xl) {
      font-size: 260px;
    }

    @media (min-width: $xxl) {
      font-size: 320px;
    }

    &__icon {
      width: 120px;
      height: 120px;

      @media (min-width: $md) {
        width: 200px;
        height: 200px;
      }

      @media (min-width: $xl) {
        width: 280px;
        height: 280px;
      }

      @media (min-width: $xxl) {
        width: 330px;
        height: 330px;
      }
    }
  }

  &-content {
    text-align: center;

    &__title,
    &__desc {
      margin-bottom: 15px;

      @media (min-width: $xl) {
        margin-bottom: 30px;
      }
    }

    &__desc {
      @media (min-width: $xs) {
        margin-left: auto;
        margin-right: auto;
        max-width: 360px;
      }

      @media (min-width: $md) {
        max-width: 550px;
      }

      @media (min-width: $xl) {
        max-width: 650px;
      }
    }
  }
}
