.registration {
  position: relative;
  padding: 15px;
  background-color: $white;

  @media (min-width: $md) {
    padding: 30px;
  }

  @media (min-width: $xl) {
    margin: 0 auto;
    padding: 50px;
    max-width: 1280px;
  }

  &__decor {
    margin: 0 auto 30px;
    width: 170px;

    @media (min-width: $md) {
      width: 195px;
    }

    @media (min-width: $xl) {
      position: absolute;
      top: 70px;
      right: 50px;
      width: 330px;
    }
  }

  h1 {
    margin-bottom: 20px;

    @media (min-width: $xl) {
      margin-bottom: 50px;
    }
  }

  &__form {
    @media (min-width: $xl) {
      display: flex;
      flex-wrap: wrap;
      gap: 50px 85px;
    }

    .form__actions {
      @media (min-width: $xs) and (max-width: $max-lg) {
        padding-right: 15px;
        padding-left: 15px;
      }

      @media (min-width: $xl) {
        order: 1;
        gap: 40px;
        flex: 0 0 520px;
        flex-wrap: nowrap;
      }

      .btn {
        @media (min-width: $md) {
          margin: 0;
          max-width: 260px;
        }
      }
    }
  }

  &-field-wrapper {
    @media (min-width: $xl) {
      flex: 1 1 100%;
    }

    .form__fields {
      @media (min-width: $xl) {
        max-width: 575px;
      }
    }
  }
}

.btn-register-bg {
  background-color: $txt-clr;
  color: $white;

  @media (any-hover: hover) {
    &:hover {
      box-shadow: inset 0 0 0 2.1em $white;
      color: $txt-clr;
    }
  }

  &__icon {
    width: 18px;
    height: 18px;
  }
}
