@font-face {
  font-family: "Nunito-Extra-Light";
  src: url("../fonts/nunito/nunito-200.woff2") format("woff2");
  font-display: swap;
  font-weight: 200;
  font-style: normal;
}
@font-face {
  font-family: "Nunito";
  src: url("../fonts/nunito/nunito-400.woff2") format("woff2");
  font-display: swap;
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "Nunito-Semi-Bold";
  src: url("../fonts/nunito/nunito-600.woff2") format("woff2");
  font-display: swap;
  font-weight: 600;
  font-style: normal;
}
h1, .h1,
h2, .h2,
h3, .h3,
h4, .h4,
h5, .h5,
h6, .h6,
p {
  margin-top: 0;
}

h1, .h1,
h2, .h2 {
  margin-bottom: 0;
  font-family: "Nunito-Extra-Light", "Helvetica", "Verdana", "Arial", sans-serif;
  font-weight: 200;
  line-height: 1.1;
}

h1,
.h1 {
  font-size: 24px;
}
@media (min-width: 768px) {
  h1,
  .h1 {
    font-size: 28px;
  }
}
@media (min-width: 1440px) {
  h1,
  .h1 {
    font-size: 48px;
  }
}

h2,
.h2 {
  font-size: 20px;
}
@media (min-width: 768px) {
  h2,
  .h2 {
    font-size: 24px;
  }
}
@media (min-width: 1440px) {
  h2,
  .h2 {
    font-size: 36px;
  }
}

p:last-child {
  margin-bottom: 0;
}

a {
  color: #016275;
  text-decoration: none;
  transition: 0.2s ease;
}
@media (any-hover: hover) {
  a:hover {
    color: #967d66;
  }
}

*,
::after,
::before {
  box-sizing: border-box;
}

a,
input,
button,
select,
textarea {
  outline: none;
}

svg,
img {
  display: block;
  width: 100%;
  height: 100%;
}

img {
  max-width: 100%;
  object-fit: cover;
}

ul,
ol,
dl {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none;
}

html {
  font-size: 12px;
}
@media (min-width: 768px) {
  html {
    font-size: 14px;
  }
}
@media (min-width: 1440px) {
  html {
    font-size: 18px;
  }
}

body {
  font-family: "Nunito-Extra-Light", "Helvetica", "Verdana", "Arial", sans-serif;
  line-height: 1.33;
  color: #161616;
  background-color: #f9f5f1;
}

::selection {
  color: #f9f5f1;
  background-color: #967d66;
}

.main {
  margin-top: 60px;
  padding-top: 20px;
  padding-bottom: 50px;
}
@media (min-width: 768px) {
  .main {
    margin-top: 65px;
    padding-bottom: 75px;
  }
}
@media (min-width: 1440px) {
  .main {
    padding-top: 30px;
    padding-bottom: 110px;
  }
}
@media (min-width: 1920px) {
  .main {
    margin-top: 70px;
    padding-top: 50px;
    padding-bottom: 120px;
  }
}

.container {
  padding-right: 15px;
  padding-left: 15px;
  width: 100%;
}
@media (min-width: 768px) {
  .container {
    padding-right: 20px;
    padding-left: 20px;
  }
}
@media (min-width: 1440px) {
  .container {
    margin: 0 auto;
    padding-right: 60px;
    padding-left: 60px;
    max-width: 1840px;
  }
}

.text-page-heading {
  text-align: center;
}
.text-page-heading, .text-page-heading__tip {
  margin-bottom: 20px;
}
@media (min-width: 1440px) {
  .text-page-heading, .text-page-heading__tip {
    margin-bottom: 30px;
  }
}
.text-page-heading__tip {
  display: block;
}
.text-page-image {
  margin-bottom: 20px;
  height: 180px;
}
@media (min-width: 768px) {
  .text-page-image {
    height: 350px;
  }
}
@media (min-width: 1440px) {
  .text-page-image {
    margin-bottom: 30px;
    height: 560px;
  }
}
@media (min-width: 1920px) {
  .text-page-image {
    height: 780px;
  }
}

.section__title, .section__desc {
  margin-bottom: 15px;
}
@media (min-width: 768px) {
  .section__title, .section__desc {
    margin-bottom: 20px;
  }
}
@media (min-width: 1440px) {
  .section__title, .section__desc {
    margin-bottom: 30px;
  }
}

.hidden {
  display: none !important;
}

.no-scroll {
  overflow: hidden;
}

.btn {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  margin: 0 auto;
  max-width: 180px;
  width: 100%;
  height: 40px;
  font-size: 10px;
  font-family: "Nunito-Semi-Bold", "Helvetica", "Verdana", "Arial", sans-serif;
  color: #016275;
  background-color: transparent;
  border: 1px solid #016275;
  cursor: pointer;
  transition: 0.2s ease;
}
@media (min-width: 1440px) {
  .btn {
    max-width: 240px;
    height: 50px;
    font-size: 12px;
  }
}
@media (any-hover: hover) {
  .btn:hover {
    box-shadow: inset 0 0 0 2.1em #016275;
    color: #f9f5f1;
  }
}
.btn--bg {
  color: #f9f5f1;
  border: 0;
  background: linear-gradient(278deg, #efe5dc -90.03%, #016275 56.92%);
}
.btn--bg:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(98deg, #efe5dc -90.03%, #016275 56.92%);
  opacity: 0;
  transition: opacity 0.2s ease;
}
@media (any-hover: hover) {
  .btn--bg:hover {
    box-shadow: none;
  }
  .btn--bg:hover:after {
    opacity: 1;
  }
}
.btn--bg span {
  position: relative;
  z-index: 1;
}

.popup {
  padding-top: 20px;
  padding-bottom: 20px;
}
@media (min-width: 768px) {
  .popup {
    padding-top: 40px;
    padding-bottom: 40px;
  }
}
@media (min-width: 1920px) {
  .popup {
    padding-top: 50px;
    padding-bottom: 50px;
  }
}
.popup-wrapper {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 0;
  height: 0;
  transition-delay: 0.2s;
}
.popup-wrapper.is-open {
  width: 100vw;
  height: 100vh;
  background-color: rgba(150, 125, 102, 0.3);
  transition-delay: unset;
}
.popup__close {
  margin-left: auto;
  margin-bottom: 20px;
  width: 16px;
  height: 16px;
  transition: 0.2s ease;
  cursor: pointer;
}
@media (min-width: 768px) {
  .popup__close {
    margin-bottom: 25px;
  }
}
@media (min-width: 1440px) {
  .popup__close {
    margin-bottom: 45px;
  }
}
@media (any-hover: hover) {
  .popup__close:hover {
    transform: rotate(90deg);
  }
}
.popup__container {
  position: relative;
  padding-right: 15px;
  padding-left: 15px;
}
@media (min-width: 390px) {
  .popup__container {
    padding-right: 20px;
    padding-left: 20px;
  }
}
@media (min-width: 768px) {
  .popup__container {
    padding-right: 40px;
    padding-left: 40px;
  }
}
@media (min-width: 1920px) {
  .popup__container {
    padding-right: 60px;
    padding-left: 60px;
  }
}

.form__group {
  position: relative;
}
.form__group:not(:last-child) {
  margin-bottom: 15px;
}
@media (min-width: 768px) {
  .form__group:not(:last-child) {
    margin-bottom: 20px;
  }
}
@media (min-width: 1440px) {
  .form__group:not(:last-child) {
    margin-bottom: 30px;
  }
}
.form__input {
  position: relative;
  z-index: 1;
  padding: 0;
  width: 100%;
  height: 35px;
  background: transparent;
  border: 0;
  border-bottom: 1px solid #a4a4a4;
  color: #161616;
}
@media (min-width: 1440px) {
  .form__input {
    height: 45px;
  }
}
.form__input-label {
  position: absolute;
  top: 11px;
  left: 0;
  line-height: 1;
  transition: 0.2s ease;
}
.form__input.is-focused + .form__input-label {
  top: -4px;
  font-size: 10px;
  font-weight: 600;
  color: #a4a4a4;
}
@media (min-width: 1440px) {
  .form__input.is-focused + .form__input-label {
    top: -6px;
    font-size: 12px;
  }
}
.form__fields {
  margin-bottom: 30px;
}
@media (min-width: 768px) {
  .form__fields {
    margin-bottom: 50px;
  }
}
.form__actions {
  display: flex;
  flex-wrap: wrap;
  gap: 5px;
}
@media (min-width: 768px) {
  .form__actions {
    justify-content: center;
    gap: 20px;
  }
}
@media (max-width: 767px) {
  .form__actions .btn {
    max-width: none;
  }
}
.form__agreement {
  margin-top: 30px;
  color: #967d66;
}
@media (min-width: 390px) and (max-width: 767px) {
  .form__agreement {
    padding-right: 15px;
    padding-left: 15px;
  }
}
@media (min-width: 768px) {
  .form__agreement {
    max-width: 540px;
    margin: 20px auto 0;
  }
}
@media (min-width: 1440px) {
  .form__agreement {
    margin: 0;
    flex: 0 0 575px;
    max-width: none;
  }
}
@media (min-width: 768px) {
  .form__agreement span {
    display: block;
    max-width: 370px;
  }
}
@media (min-width: 1440px) {
  .form__agreement span {
    max-width: 415px;
  }
}

.header {
  transform: translateY(-100%);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 2;
  padding-top: 20px;
  padding-bottom: 20px;
  width: 100%;
  background-color: #f6f1eb;
  box-shadow: 0 3px 20px 0 rgba(87, 79, 69, 0.1);
  transition: 0.2s ease;
}
.header.is-fixed {
  transform: translateY(0);
}
.header__content {
  display: flex;
  align-items: center;
  gap: 20px;
}
.header__content-item:not(:nth-child(2)) {
  flex: 1 1 50%;
}
@media (max-width: 1439px) {
  .header-menu {
    display: none;
  }
}
@media (min-width: 1440px) {
  .header-menu {
    display: flex;
    align-items: center;
    gap: 35px;
    font-size: 12px;
  }
}
@media (min-width: 1920px) {
  .header-menu {
    font-size: 14px;
  }
}
@media (min-width: 1440px) {
  .header-menu__link {
    color: #161616;
  }
}
.header__logo {
  display: block;
  color: #161616;
  width: 75px;
}
@media (min-width: 768px) {
  .header__logo {
    width: 95px;
  }
}
@media (min-width: 1920px) {
  .header__logo {
    width: 115px;
  }
}
.header__logo:hover {
  color: #161616;
}
.header-informers {
  display: flex;
  align-items: center;
  gap: 15px;
}
@media (min-width: 768px) {
  .header-informers {
    gap: 20px;
  }
}
@media (min-width: 1920px) {
  .header-informers:not(.header-informers--left) {
    gap: 25px;
  }
}
.header-informers--right {
  justify-content: flex-end;
}
@media (min-width: 1440px) {
  .header-informers--left {
    gap: 35px;
  }
}
.header-informers__link {
  color: #161616;
}
.header-informers__icon {
  display: block;
  width: 15px;
}
@media (min-width: 768px) {
  .header-informers__icon {
    width: 20px;
  }
}

@media (min-width: 1440px) {
  .language-mob {
    display: none;
  }
}
@media (max-width: 1439px) {
  .language-desk {
    display: none;
  }
}

@media (min-width: 1440px) {
  .search-informer .popup-wrapper {
    max-height: 300px;
  }
}
@media (min-width: 1440px) {
  .search-informer .popup {
    transform: translateY(-70px);
    width: 1000px;
    opacity: 0;
    visibility: hidden;
    transition: 0.2s ease;
  }
}
@media (min-width: 1920px) {
  .search-informer .popup {
    width: 1220px;
  }
}
.search-informer__icon {
  cursor: pointer;
  transition: 0.2s ease;
}
@media (any-hover: hover) {
  .search-informer__icon:hover {
    color: #967d66;
  }
}
@media (max-width: 1439px) {
  .search-informer--desk {
    display: none;
  }
}
.search-informer .is-open .popup {
  transform: translateY(0);
  opacity: 1;
  visibility: visible;
}

@media (min-width: 1440px) {
  .search-form .form__group {
    margin-bottom: 0;
  }
}
@media (min-width: 1440px) {
  .search-form__icon, .search-form__close {
    transform: translateY(-50%);
    position: absolute;
    top: 50%;
    z-index: 2;
    width: 25px;
    cursor: pointer;
    transition: 0.2s ease;
  }
}
@media (any-hover: hover) {
  .search-form__icon:hover, .search-form__close:hover {
    color: #967d66;
  }
}
@media (min-width: 1440px) {
  .search-form__icon {
    left: 30px;
  }
}
@media (min-width: 1440px) {
  .search-form__close {
    right: 30px;
  }
}
@media (any-hover: hover) {
  .search-form__close:hover {
    transform: translateY(-50%) rotate(90deg);
  }
}
@media (min-width: 1440px) {
  .search-form .form__input {
    padding-left: 85px;
    padding-right: 85px;
    height: 60px;
    background-color: #fff;
  }
}
@media (min-width: 1920px) {
  .search-form .form__input {
    height: 70px;
  }
}

.cart-informer {
  position: relative;
}
.cart-informer__counter {
  position: absolute;
  top: 0;
  right: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 70%;
  height: 70%;
  font-family: "Nunito", "Helvetica", "Verdana", "Arial", sans-serif;
  font-size: 6px;
  color: #161616;
  border-radius: 50%;
  background: #f6f1eb;
  border: 1px solid #161616;
  line-height: 1;
}
@media (min-width: 768px) {
  .cart-informer__counter {
    font-size: 8px;
  }
}

.mobile-menu__btn-burger {
  width: 20px;
  cursor: pointer;
}
@media (min-width: 768px) {
  .mobile-menu__btn-burger {
    width: 30px;
  }
}
@media (min-width: 1440px) {
  .mobile-menu__btn-burger {
    display: none;
  }
}
.mobile-menu__btn-burger rect {
  transition: 0.2s ease;
}
.mobile-menu__btn-burger:hover rect:first-child {
  width: 30px;
}
.mobile-menu__btn-burger:hover rect:nth-child(2) {
  width: 20px;
}
.mobile-menu__btn-burger:hover rect:nth-child(3) {
  width: 10px;
}
.mobile-menu__btn-burger:hover rect:nth-child(4) {
  width: 15px;
}
.mobile-menu__btn-burger:hover rect:nth-child(5) {
  width: 25px;
}
@media (max-width: 1439px) {
  .mobile-menu__btn-text {
    display: none;
  }
}
@media (min-width: 1440px) {
  .mobile-menu__btn-text {
    display: flex;
    gap: 5px;
    font-size: 12px;
    line-height: normal;
    cursor: pointer;
    transition: 0.2s ease;
