// Footer
.footer {
  padding-top: 50px;
  padding-bottom: 50px;
  font-family: $font-title;
  background-color: $footer-bg-clr;
  color: $txt-bg-clr;

  @media (max-width: $max-md) {
    text-align: center;
  }

  @media (min-width: $md) {
    font-size: 12px;
  }

  @media (min-width: $xxl) {
    font-size: 14px;
  }

  &__content {
    @media (min-width: $md) {
      display: flex;
      flex-wrap: wrap;
      row-gap: 50px;
    }

    @media (min-width: $xl) {
      display: grid;
      align-items: center;
      grid-template-columns: repeat(3, 1fr);
      grid-template-rows: repeat(2, 1fr);
      grid-column-gap: 0;
      grid-row-gap: 60px;
    }

    &-logo {
      @media (min-width: $md) {
        flex: 1 1 100%;
        order: -1;
      }

      @media (min-width: $xl) {
        grid-area: 1 / 2 / 3 / 3;
      }
    }
  }

  &__logo {
    display: block;
    margin: 0 auto;
    width: 95px;
    color: $txt-bg-clr;

    @media (min-width: $md) {
      width: 150px;
    }

    @media (min-width: $xl) {
      width: 170px;
    }
  }

  &__address,
  &__copyright,
  &__policy {
    @media (min-width: $md) {
      flex: 1 1 50%;
    }
  }

  &__address {
    margin-bottom: 0;

    @media (min-width: $md) {
      order: -1;
    }

    @media (min-width: $xl) {
      grid-area: 1 / 1 / 2 / 2;
    }
  }

  &__copyright {
    @media (max-width: $max-md) {
      margin-top: 30px;
      margin-bottom: 30px;
    }

    @media (min-width: $md) {
      margin-bottom: 0;
    }

    @media (min-width: $xl) {
      grid-area: 2 / 1 / 3 / 2;
    }
  }

  &__policy {
    color: $txt-bg-clr;

    @media (min-width: $md) {
      text-align: right;
    }

    @media (min-width: $xl) {
      grid-area: 2 / 3 / 3 / 4;
    }
  }
}

// Social
.social {
  display: flex;
  align-items: center;

  @media (max-width: $max-md) {
    justify-content: space-between;
    margin-top: 50px;
    margin-bottom: 50px;
  }

  @media (min-width: $md) {
    justify-content: flex-end;
    gap: 20px;
    flex: 1 1 50%;
  }

  @media (min-width: $xl) {
    grid-area: 1 / 3 / 2 / 4;
  }

  &__link {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 37px;
    height: 37px;
    border: 1px solid $txt-bg-clr;
    border-radius: 50%;
    color: $txt-bg-clr;

    svg {
      width: auto;
      height: auto;
    }
  }
}
