// Collections
.collection {
  margin-top: 50px;
  margin-bottom: 50px;

  @media (min-width: $md) {
    display: flex;
    align-items: center;
    gap: 20px;
  }

  @media (min-width: $xl) {
    gap: 30px;
  }

  &__image,
  &__content {
    @media (min-width: $md) {
      flex: 1 1 50%;
    }
  }

  &__image {
    height: 360px;

    @media (max-width: $max-md) {
      margin-bottom: 20px;
    }

    @media (min-width: $xl) {
      height: 560px;
    }

    @media (min-width: $xxl) {
      height: 730px;
    }
  }

  &__content {
    text-align: center;

    @media (min-width: $md) {
      margin: 0 auto;
      max-width: 550px;
    }

    @media (min-width: $xl) {
      max-width: 650px;
    }

    &-title,
    &-desc {
      margin-bottom: 15px;

      @media (min-width: $md) {
        margin-bottom: 20px;
      }

      @media (min-width: $xl) {
        margin-bottom: 30px;
      }
    }
  }

  &--wide {
    @media (min-width: $md) {
      flex-wrap: wrap;
    }

    .collection__image,
    .collection__content {
      @media (min-width: $md) {
        flex-basis: 100%;
      }
    }

    .collection__content {
      @media (min-width: $md) {
        max-width: 425px;
      }

      @media (min-width: $xl) {
        max-width: 550px;
      }

      @media (min-width: $xxl) {
        max-width: 650px;
      }
    }
  }
}
