h1,.h1,
h2,.h2,
h3,.h3,
h4,.h4,
h5,.h5,
h6,.h6,
p {
  margin-top: 0;
}

h1,.h1,
h2,.h2 {
  margin-bottom: 0;
  font-family: $font-txt;
  font-weight: 200;
  line-height: 1.1;
}

h1,
.h1 {
  font-size: 24px;

  @media (min-width: $md) {
    font-size: 28px;
  }

  @media (min-width: $xl) {
    font-size: 48px;
  }
}

h2,
.h2 {
  font-size: 20px;

  @media (min-width: $md) {
    font-size: 24px;
  }

  @media (min-width: $xl) {
    font-size: 36px;
  }
}

p:last-child {
  margin-bottom: 0;
}

a {
  color: $main-clr;
  text-decoration: none;
  transition: $transition;

  @media (any-hover: hover) {
    &:hover {
      color: $secondary-clr;
    }
  }
}
