// Language
.language-informer {
  position: relative;
  display: flex;
  align-items: center;
  gap: 10px;

  &__icon,
  &__name {
    z-index: 1;
  }

  &__icon {
    display: block;
    width: 15px;
    transition: $transition;
    cursor: pointer;

    @media (min-width: $md) {
      width: 20px;
    }

    @media (any-hover: hover) {
      &:hover {
        color: $secondary-clr;
      }
    }
  }

  &__name {
    font-family: $font-title;
    font-size: 10px;
    line-height: 1;
    pointer-events: none;
  }
}

// Language menu
.language-menu {
  position: absolute;
  top: -10px;
  opacity: 0;
  visibility: hidden;
  transition: .3s ease;

  @media (max-width: $max-xl) {
    left: -7px;
  }

  @media (min-width: $xl) {
    right: -10px;
  }

  &.is-open {
    opacity: 1;
    visibility: visible;
  }

  &__item {
    &:not(:last-child) {
      margin-bottom: 4px;
    }

    &--top {
      background: $white;
      width: 60px;
      height: 35px;

      @media (min-width: $md) {
        width: 160px;
        height: 40px;
      }
    }
  }

  &__link {
    display: block;
    padding: 10px;
    width: 160px;
    height: 30px;
    font-family: $font-title;
    font-size: 10px;
    text-transform: uppercase;
    color: $txt-clr;
    background-color: $white;
    text-align: center;
  }
}
