// Profile
.profile-informer {
  position: relative;

  &__icon {
    cursor: pointer;
    transition: $transition;

    @media (any-hover: hover) {
      &:hover {
        color: $secondary-clr;
      }
    }
  }

  &__is-logged {
    position: absolute;
    right: -1px;
    bottom: -1px;
    color: $txt-clr;

    @media (min-width: $md) {
      width: 12px;
      height: 12px;
    }
  }
}

.auth-popup {
  transform: translateY(-70px);
  width: 100%;
  opacity: 0;
  visibility: hidden;
  transition: $transition;

  @media (min-width: $sm) {
    max-width: 610px;
  }

  @media (min-width: $xl) {
    max-width: 710px;
  }

  &__content {
    margin-right: 15px;
    margin-left: 15px;
    padding: 20px;
    background-color: $bg-clr;

    @media (min-width: $md) {
      padding: 30px;
    }

    @media (min-width: $xl) {
      padding: 50px;
    }
  }

  &__heading {
    position: relative;
    margin-bottom: 30px;

    @media (min-width: $md) {
      padding-right: 20px;
      padding-left: 20px;
    }

    @media (min-width: $xl) {
      margin-bottom: 60px;
      padding-right: 50px;
      padding-left: 50px;
    }

    a {
      position: absolute;
      right: 0;
      bottom: 0;
      color: $txt-clr;

      @media (min-width: $md) {
        right: 20px;
      }

      @media (min-width: $xl) {
        right: 50px;
      }
    }
  }

  &__form {
    @media (min-width: $md) {
      padding-right: 20px;
      padding-left: 20px;
    }

    @media (min-width: $xl) {
      padding-right: 50px;
      padding-left: 50px;
    }
  }

  .form__actions {
    gap: 10px;
    margin-top: 30px;

    @media (min-width: $md) {
      flex-wrap: nowrap;
      margin-top: 60px;
    }

    .btn {
      @media (min-width: $md) {
        max-width: none;
      }
    }
  }
}

.is-open .auth-popup {
  transform: translateY(0);
  opacity: 1;
  visibility: visible;
}
